import './FabricantePedidos.css'

import { IonSpinner } from '@ionic/react'
import { observer } from 'mobx-react-lite'
import React, { useEffect, useState } from 'react'
import { RouteComponentProps, useLocation } from 'react-router'

import { Page } from '../../components'
import { PEDIDO_ESTADOS, RUTAS_APP_FABRICANTE } from '../../constants'
import {fecha_fmt, plata_fmt} from '../../helpers'
import { cambiarModo, useOpenPage } from '../../helpers-ts'
import { ICONOS } from '../../helpers-ts/iconos'
import { useStores } from '../../models/root-store/root-store-context'
import { Pedido } from '../../services/api'
import { IoChatbubbleEllipsesOutline } from 'react-icons/io5'
import { Capacitor } from '@capacitor/core'
import { StatusBar } from '@ionic-native/status-bar'

const moment = require('moment-timezone')

export const FabricantePedidos: React.FC<RouteComponentProps> = observer(({ history }) => {
  const rootStore = useStores()
  const { loginStore } = rootStore
  const [Pedidos, setPedidos] = useState([] as Pedido[])
  const [PathInicial, setPathInicial] = useState(window.location.pathname)
  const [Loading, setLoading] = useState(false)
  const IntervalID = React.useRef(null as any)
  const [FiltroEstado, setFiltroEstado] = useState<number | null>(null)
  const FiltroEstadoRef = React.useRef(null as any)
  const Movil = window.innerWidth <= 500 || /Mobi/.test(navigator.userAgent)

  const openPage = useOpenPage()
  const { search } = useLocation()

  const estados = [
    { value: 0, label: 'Creado' },
    { value: 10, label: 'Confirmado' },
    { value: 20, label: 'Finalizado' },
  ]

  React.useEffect(() => {
    if (Capacitor.getPlatform() === 'android') {
      StatusBar.backgroundColorByHexString('#2f39b2')
    } else if (Capacitor.getPlatform() === 'ios') {
      document.body.style.background = '#2f39b2'
    } 
    obtenerFabricante()
    //
    setLoading(true)
    if (window.location.pathname.indexOf('/pendiente') !== -1) {
      setFiltroEstado(0)
      FiltroEstadoRef.current = 0
    } else if (window.location.pathname.indexOf('/retiro') !== -1) {
      setFiltroEstado(10)
      FiltroEstadoRef.current = 10
    } else {
      obtenerVentas()
    }
  }, [window.location.pathname])

  useEffect(() => {
    IntervalID.current = setInterval(() => {
      if (PathInicial !== rootStore.currentPath) {
        return
      }
      obtenerVentas()
    }, 10000)
    return () => {
      clearInterval(IntervalID.current)
    }
  }, [])

  React.useEffect(() => {
    const currentUrlParams = new URLSearchParams(search)
    if (currentUrlParams.get('r')) {
      obtenerVentas()
    }
  }, [search])

  const obtenerVentas = () => {
    rootStore.environment.api.adminFabricanteVentas('', FiltroEstadoRef.current).then((result) => {
      if (result.kind === 'ok') {
        const pedidos = (result.data as any).results
        setPedidos(pedidos)
      }
      setLoading(false)
    })
  }

  const obtenerFabricante = () => {
    if (!loginStore.fabricante_id) {
      return
    }
    rootStore.environment.api.getFabricante(loginStore.fabricante_id).then((result) => {
      if (result.kind === 'ok') {
        loginStore.setFabricante(result.fabricante)
        //
        if (result.fabricante.bloqueado) {
          alert('Su perfil de proveedor se encuentra deshabilitado, por favor contacte con la administración')
          // Cambiar a modo cliente
          cambiarModo(rootStore, history, 10)
        }
      }
    })
  }

  useEffect(() => {
    obtenerVentas()
  }, [FiltroEstado])

  return (
    <Page login_required fabricante={true} disableScroll={true} id={'pedidos-fabricante'}>
      {Movil && (
        <div className="d-flex flex-row p-3" style={{ gap: '1rem' }}>
          <div
            className={`btn flex-grow-1 ${FiltroEstado === 0 ? 'btn-primary' : 'btn-outline-primary'}`}
            onClick={() => {
              setFiltroEstado(0)
              FiltroEstadoRef.current = 0
              obtenerVentas()
            }}
          >
            Pendientes
          </div>
          <div
            className={`btn flex-grow-1 ${FiltroEstado === 10 ? 'btn-primary' : 'btn-outline-primary'}`}
            onClick={() => {
              setFiltroEstado(10)
              FiltroEstadoRef.current = 10
              obtenerVentas()
            }}
          >
            Retiro
          </div>
        </div>
      )}
      <div className={`pedidos-list-container ${Movil ? 'container-lg' : ''}`}>
        {Loading && <IonSpinner name="crescent" className={'loadingSpinnerFabricanteApp pt-5'} />}
        {!Loading &&
          Pedidos.map((item: any) => {
            const enProceso = item.pedido.estado === PEDIDO_ESTADOS.en_proceso
            let estilo_input = `pedido ${item.estado_itinerario === 15 ? 'en-destino' : 'en-proceso'}`
            if (item.estado === 0) {
              estilo_input = estilo_input + ' pendiente'
            }
            if (item.estado === 10) {
              estilo_input = estilo_input + ' aprobado'
            }
            return (
              <div
                className={estilo_input}
                key={'pedido-' + item.id}
                onClick={openPage(RUTAS_APP_FABRICANTE.VENTA_DETAIL(item.id))}
              >
                <div className={'d-flex flex-column flex-grow-1'}>
                  <div
                    className={'referencia mt-2'}
                    style={!enProceso ? { color: 'var(--azul-fabricante)' } : {}}
                  >
                    Pedido #{item.referencia}
                  </div>
                  {!enProceso && <div className={'productos'}>{item.productos_str}</div>}
                  {!enProceso && <div className={'monto'}>Monto: {plata_fmt(item.costo_total)}</div>}
                  {enProceso && (
                    <>
                      <div className={'productos'}>
                        Driver: {item.pedido.transportista.usuario.first_name}{' '}
                        {item.pedido.transportista.usuario.last_name}, {item.vehiculo.marca} {item.vehiculo.modelo}
                      </div>
                      <div>Hora de retiro: {moment(item.fecha_retiro).format('hh:mm')}</div>
                      <div className="mb-2">
                        <strong>{item.estado_itinerario === 15 ? 'Llegó el Driver' : 'En camino'}</strong>
                      </div>
                    </>
                  )}
                </div>
                <div className="boton-chat-container d-flex flex-column justify-content-center mb-2 mb-md-0">
                  {enProceso
                    ? <IoChatbubbleEllipsesOutline
                        color="var(--naranja-transportista)"
                        size={45}
                        style={{ transform: 'rotateY(180deg)' }}
                      />
                    : <div>
                        <strong>Entrada:</strong> <br/>
                        <span> {fecha_fmt(item.pedido.fecha, 'hh:mm a')}</span>
                      </div>
                  }

                </div>

                <div
                  className="boton-retiro"
                  style={enProceso ? { backgroundColor: 'var(--naranja-transportista)' } : {}}
                >
                  {!enProceso && <span className="caja">{ICONOS.stock}</span>}
                  {!enProceso && <span>Datos de retiro</span>}
                  {enProceso && (
                    <span className="caja">
                      <img src="assets/icon/scanner_qr.png" />
                    </span>
                  )}
                  {enProceso && <span className="ml-2 ml-md-0">Código QR</span>}
                </div>
              </div>
            )
          })}
        {!Loading && Pedidos.length === 0 && (
          <div className={'d-flex mt-5'}>
            <span className={'mx-auto my-auto'}>No se encontraron pedidos</span>
          </div>
        )}
      </div>
    </Page>
  )
})

export default FabricantePedidos
